"use client";
import React from "react";
import Head from "next/head";
import Link from "next/link";
import { Card, CardContent } from "src/components/ui/card";
import Layout from "src/components/ui/Layout";
import { TeamGallery } from "src/components/ui/TeamGallery";
import { Button } from "src/components/ui/button";
import { Navbar } from "src/components/ui/Navbar";

export default function ContactPage() {
  return (
    <Layout>
      <Head>
        <title>Contact Us | AAT Business Solutions</title>
      </Head>
      <Navbar />

      <div className="min-h-screen bg-white text-gray-800 font-sans pt-20 px-4 md:px-6">

        {/* Hero Section */}
        <section className="py-16 md:py-20 text-center px-4 md:px-6 mb-12 rounded-lg shadow-sm bg-white">
          <div className="max-w-4xl mx-auto">
            <h1 className="text-3xl md:text-5xl font-extrabold text-black mb-4 leading-tight">
              <span className="bg-clip-text text-transparent bg-gradient-to-r from-blue-700 to-red-700">
                Get in Touch with AAT Business Solutions
              </span>
            </h1>
            <p className="text-base md:text-xl text-gray-700 max-w-2xl mx-auto mb-8">
              We’d love to hear from you! Whether you’re curious about our services or just want to say hi — reach out, we’re here.
            </p>
            <div className="flex flex-col sm:flex-row sm:justify-center gap-4">
              <a href="tel:+27816515179">
                <Button size="lg" className="rounded-md shadow-md hover:shadow-lg transition-all duration-300 w-full sm:w-auto">
                  📞 Call Us
                </Button>
              </a>
              <a href="mailto:admin@aatsolutions.co.za">
                <Button size="lg" variant="outline" className="rounded-md shadow-md hover:shadow-lg transition-all duration-300 w-full sm:w-auto">
                  📧 Send an Email
                </Button>
              </a>
            </div>
          </div>
        </section>

        {/* Main Content */}
        <div className="max-w-6xl mx-auto pb-16 flex flex-col md:flex-row justify-between items-start gap-12">

          {/* Left: Contact Info */}
          <div className="md:w-2/3 w-full">
            <h2 className="text-2xl md:text-3xl font-bold mb-8 text-black border-b-2 pb-2 border-indigo-200">
              Contact Information
            </h2>

            <Card className="bg-white border border-gray-200 shadow-lg rounded-xl mb-10">
              <CardContent className="p-8">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-10">
                  <div>
                    <h3 className="text-lg font-semibold mb-2">📍 Our Office</h3>
                    <p className="text-sm">
                      <a
                        href="https://www.google.com/maps?q=8+Fred+Verseput+Road,+Halfway+Gardens+1686,+Midrand"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="hover:underline block"
                      >
                        8 Fred Verseput Road<br />
                        Halfway Gardens 1686, Midrand
                      </a>
                    </p>
                  </div>
                  <div>
                    <h3 className="text-lg font-semibold mb-2">📞 Contact</h3>
                    <p className="text-sm">
                      <a href="tel:+27816515179" className="hover:underline">+27 81 651 5179</a><br />
                      <a href="mailto:admin@aatsolutions.co.za" className="hover:underline">admin@aatsolutions.co.za</a>
                    </p>
                  </div>
                  <div>
                    <h3 className="text-lg font-semibold mb-2">💬 Other Ways</h3>
                    <p className="text-sm">
                      <a
                        href="https://wa.me/27816515179?text=Hello, I'm contacting you from your website"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="hover:underline"
                      >
                        Message us on WhatsApp
                      </a>
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <h2 className="text-2xl md:text-3xl font-bold mb-6 text-black border-b-2 pb-2 border-indigo-200">
              Meet Our Team
            </h2>

            <Card className="bg-white border border-gray-200 shadow-lg rounded-xl">
              <CardContent className="p-8">
                <TeamGallery />
              </CardContent>
            </Card>
          </div>

          {/* Right: Sticky CTA (Optional Add-on) */}
          <Card className="w-full md:w-[320px] border border-gray-200 shadow-xl rounded-lg sticky md:top-28 self-start bg-white">
            <CardContent className="p-6 flex flex-col h-full text-center">
              <h3 className="font-bold text-xl mb-3 text-black">Let's Connect</h3>
              <p className="text-sm text-gray-600 mb-5 leading-relaxed">
                Need more info or want to collaborate? We’re just a message away.
              </p>
              <div className="flex flex-col gap-3">
                <a href="mailto:admin@aatsolutions.co.za">
                  <Button className="w-full py-2 rounded-md">📧 Email Us</Button>
                </a>
                <a href="https://wa.me/27816515179" target="_blank" rel="noopener noreferrer">
                  <Button variant="outline" className="w-full py-2 rounded-md">📱 WhatsApp Us</Button>
                </a>
              </div>
            </CardContent>
          </Card>

        </div>
      </div>
    </Layout>
  );
}
